	/* RESPONSIVE CLASSES */
	.desktop-hide,
	.mobile-only,
	.mobile-only-block-full,
	.mobile-only-table-full {
	display: none;
	max-height: 0px;
	mso-hide: all;
	overflow: hidden;
	visibility: hidden;
	}
	/* display: none; max-height: 0px; overflow: hidden; visibility: hidden; mso-hide: all; */
	@media screen and (max-width: 480px) {
	table[class="main-table"],
	table[class="table-responsive"] {
	width: 100% !important;
	}
	img[class="img-responsive"],
	img[class="fullwidth"]{
	height: auto !important;
	width: 100% !important;
	}
	.show, *[class~="show"],
	.desktop-hide,
	*[class~="desktop-hide"],
	.mobile-only,
	*[class~="mobile-only"] {
	display: block !important;
	max-height: none !important;
	overflow: visible !important;
	visibility: visible !important;
	}
	.mobile-only-block-full,
	*[class~="mobile-only-block-full"] {
	clear: both !important;
	float: none !important;
	display: block !important;
	max-height: none !important;
	height: auto !important;
	width: 100% !important;
	overflow: visible !important;
	visibility: visible !important;
	}
	.mobile-only-table-full,
	*[class~="mobile-only-table-full"] {
	clear: both !important;
	float: none !important;
	display: table !important;
	max-height: none !important;
	height: auto !important;
	width: 100% !important;
	overflow: visible !important;
	visibility: visible !important;
	}
	.hide, *[class~="hide"],
	.desktop-only,
	*[class~="desktop-only"],
	.mobile-hide,
	*[class~="mobile-hide"] {
	display: none !important;
	height: 0 !important;
	margin: 0 !important;
	max-height: 0 !important;
	overflow: hidden !important;
	padding: 0 !important;
	visibility: hidden !important;
	width: 0 !important;
	}
	.stack-col,
	*[class~="stack-col"] {
	width: 100%;
	box-sizing: border-box;
	clear: both !important;
	float: none !important;
	display: block !important;
	}
	/* Add your responsive classes here */
	.p0, *[class~="p0"] {
	padding: 0 !important;
	}
	.px20, *[class~="px20"] {
	padding-left: 20px !important;
	padding-right: 20px !important;
	}
	.center, *[class~="center"] {
	text-align: center !important;
	}
	/* if class defined with *[class~=] then you can combine names i.e: class="px20 center"*/
	
	}
